:root{
    --primary: #fff;
}

.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary{
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}
.contact-container {
    background-color: #242424;
    padding: 10rem 0 10rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .contact-subscription {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;
  }
  
  .contact-subscription > p {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .contact-subscription-text {
    margin-bottom: 24px;
    font-size: 20px;
  }
  
  .contact-input {
    padding: 8px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff;
  }
  
  .contact-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
  }
  
  .contact-link-wrapper {
    display: flex;
  }
  
  .contact-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
  }
  
  .contact-link-items h2 {
    margin-bottom: 16px;
  }
  
  .contact-link-items > h2 {
    color: #fff;
  }
  
  .contact-link-items a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
  }
  
  .contact-link-items a:hover {
    color: #e9e9e9;
    transition: 0.3s ease-out;
  }
  
  .contact-email-form h2 {
    margin-bottom: 2rem;
  }
  
  .contact-input::-webkit-input-placeholder {
    color: #b1b1b1;
  }
  
  .contact-input:-ms-input-placeholder {
    color: #b1b1b1;
  }
  
  .contact-input::placeholder {
    color: #b1b1b1;
  }
  
  /* Social Icons */
  .contact-icon-link {
    color: #fff;
    font-size: 24px;
  }
  
  .contact-media {
    max-width: 1000px;
    width: 100%;
  }
  
  .contact-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 40px auto 0 auto;
  }
  
  .contact-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
