  }
  
  .social-logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .website-rights {
    color: #fff;
    align-items: center;
    margin-bottom: 16px;
  }
  
  @media screen and (max-width: 820px) {
    .contact-links {
      padding-top: 2rem;
    }
  
    .contact-input {
